.post {
  h1 {
    display: inline-block;
    order: 2;
    padding-top: 3.75rem;
    @media screen and (min-width: $break) and (min-height: 500px) {
      padding-top: 0;
    }
  }
}
.dayone-music {
  .dayone-album {
    display: inline-block;
    margin: 0 2px 0 0;
  }
  .music-note {
    margin-left: 4px;
    vertical-align: bottom;
  }
}
